  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Press Start 2P', 'Montserrat', sans-serif;
  box-sizing: border-box;
}
:root{
  --red: #fe5350;
  --yellow: rgb(254, 202, 27);
  --black: #14171c;
  --lightbg: rgba(0, 0, 0, 0.1)
}
