.bg {
  // min-width: 1200px;
  // min-height: 520px;
  // min-height: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  // background-attachment: fixed;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  // padding-top: 1.5em;
  // height: 100vh;
