}
.detail-frame {
  margin-top: 1em !important;
}
img {
  font-size: 12px;
}




@media (max-device-width: 765px) and (orientation: portrait) {
  .bg {
    z-index: 999;
    img, .rotate-div {
      display: block;
    }
  } 
}

@media only screen and (max-device-width: 900px){
  .play-pannel .top-div {
    flex-direction: initial;;
  }
  .top-div-active .teamDiv {
        flex-direction: column;
    height: 60% !important;
  }
  .winlose-div {
    justify-content: space-around !important;
  }
}

@media (max-device-width: 1100px) and (orientation: portrait){

  .Home {
    width: 76%;
    height: fit-content;
    align-self: center;
    .menu {
      width: 100%;
      a {
        padding: 1em 0;
      }
    }
  }
  .pokesDiv, .play-pannel .top-div {
    margin: 0 auto !important;
    width: 76%;
  }
  .pagination {
    button {
      font-size: 2em !important;
    }
    .next {
      right: 0 !important;
      left: inherit !important;
    }
    .prev {
      left: 0 !important;
    }
  }
  .top-div-active .teamDiv {
    width: 100%;
    font-size: 1.6em;
    margin: 1em auto !important;
  }
  .ScoreSheet {
    font-size: 1.2em;
  }
  .play-pannel {
    .card img {
      width: 120px;
    }
  }
  .pokesDiv {
    
    grid-template-columns: repeat(3, 1fr) !important;
    width: 76%;
    margin: 0 auto;
    .pokeCard {
      z-index: 999;
      font-size: 1em !important;
      * {
        font-size: 26px;
      }
    }
  }
  .SearchBar > input:valid {
    width: 500px;
  }
  .replayBtn {
    width: 120px;
    height: 120px;
    font-size: 1.5em;
  }
  .detail-frame{
    section {
      flex-direction: column;
      align-items: center;
      h1 {
        font-size: 1.6em;
      }
      .members {
        width: 90% !important;
        .member {
          font-size: 1.6em;
          line-height: 1.6em;
          img {
            width: 160px;
          }
        }
      }
    }
  } 

}

