@media (max-device-width: 500px) and (orientation: portrait){
  nav {
    flex-direction: column;
    height: 100%;
    .SearchBar-div {
      align-self: flex-start;
      margin: 1.5em;
    }
    .SearchBar{
      &:after {
        width: 5px;
        height: 50px;
        top: 70px;
        right: -15px;
        transform: rotate(120deg);
      }
      input {
        font-size: 2.2em;
        border-radius: 50%;      
        &:focus, &:valid {
          width: 1100px;
          border-radius: 50px;
        }  
      }
    }
    ul {
      width: 100%;
      display: grid !important;
      grid-template-columns: repeat(3, 1fr);
      .btn {
        font-size: 37px;
      }
    }
  }
  .play-pannel {
    height: fit-content;
    align-self: center;
    z-index: 99;
    font-size: 26px;
    .top-div {
      width: 76%;
      margin: 1.5em auto;
      flex-direction: column;
      height: auto;
      font-size: 1.6em;
      line-height: 1.6em;
      .winlose-div {
        height: unset;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        * {
          font-size: 1.6em;
        }
      }
      .remark {
        order: 1;
        font-size: 30px;
      }
    }
    .pagination {
      button {
        font-size: 2em;
        transform: unset;
        position: unset;
        border-radius: 50%;
        z-index: 99999;
        margin: 0;
      }
      .next {
        left: 90%;
      }
      .prev {
        right: 0;
      }
    }
  }
  .replayBtn {
    width: 200px;
    height: 200px;
  }
  .scroll-active {
    max-height: 50vh !important;
  }
