    }
    ul {
      display: flex;
    }
    .menu {
      // display: flex;
      flex-direction: column;
      a {
          text-decoration: none;
          &:first-child {
            margin-bottom: 1.1em;
          }
        }
    }
    .small-btn-div {
      display: flex;
    }
  }
 